<template>
<div class="spiderList">
  <div class="spiderList-titleBar">
    <h2>{{$route.params.name}} Data List <span> </span></h2>
    <router-link tag="button" :to="{name:'Form',query:{action:'add',cate:$route.params.id,name:$route.params.name,redirect:$route.path}}"> </router-link>
  </div>
<div class="success" v-show="showSuccess">
   ~
</div>
<table border="1" width="100%">
  <tr>
    <th>ID</th>
    <th>COVER</th>
    <th>enName</th>
    <th>cnName</th>
    <th></th>
    <th>Date</th>
    <th></th>
  </tr>
  <tr v-for="list in dataList.data" :key="list.id" :style="{background:list.status?'':'#ffd9a0'}">
    <td align="center">{{list.status===0?'':list.id}}</td>
    <td width="40">
      <img :src="$conf.qnUrl+list.cover+'?imageView2/5/w/50/h/50'"width="40">
    </td>
    <td>{{list.enName}}{{list.status===0?' ()':''}}</td>
    <td>{{list.cnName}}</td>
    <td align="center">
      {{list.isScale?'':''}}
    </td>
    <td>{{list.date}}</td>
    <td align="center">
      <router-link tag="a"
                   :to="{
                     name:'Form',
                     query:{
                       action:'edit',
                       cate:$route.params.id,
                       id:list.id,
                       name:$route.params.name,
                       redirect:$route.path
                       }}">
                        Edit
                       </router-link> |
      <a href="javascript:;" @click.stop="deleteItem(list.id)" class="del"> Delete</a>
    </td>
  </tr>
</table>
<div class="empty" v-show="dataList.count === 0">
   
</div>
<zpagenav :page="dataList.currentPage"
          :page-size="dataList.pagesize"
          :total="dataList.count"
          :max-link="5"
          :page-handler="pageHandler"
          :create-url="createUrl"></zpagenav>
</div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  name: 'SpiderList',
  data () {
    return {
      showSuccess: false
    }
  },
  async created () {
    await this.getChildList()
  },
  computed: {
    dataList () {
      return this.$store.state.Spider.childList
    }
  },
  watch: {
    '$route.params': async function () {
      await this.getChildList()
    }
  },
  methods: {
    ...mapActions([
      'getChildList',
      'deleteItem'
    ]),
    pageHandler (page) {
      this.$router.push({
        name: 'List',
        params: {
          page: page
        }
      })
    },
    createUrl () {
      return ''
    },
    async deleteItem (id) {
      const action = window.confirm('')
      if (action) {
        await this.$store.dispatch('deleteItem', id)
        clearTimeout(this.timeOut)
        this.showSuccess = true
        this.timeOut = setTimeout(() => {
          this.showSuccess = false
        }, 1000)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.spiderList{
  flex-grow:1;
  padding: 10px;
  overflow-y: scroll;
    &::-webkit-scrollbar {
      background-color: #fffdee;
      width: 10px;
    }
    &::-webkit-scrollbar-thumb{
      background-color: #e1f2ff;
    }
  .spiderList-titleBar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    h2{
      span{
        font-size: 12px;
        background: #ffd9a0;
        padding: 0 4px;
      }
    }
    button{
      background: #00ff00;
    }
  }

  .empty, .success{
    height: 30px;
    text-align: center;
    line-height: 30px;
    background: #fffdee;
    border: 1px solid #d7c985;
  }
  .success{
    background: #ebffeb;
    border: 1px solid #78de9b;
    margin-bottom: 10px;
  }
}
</style>




